body {
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

#app {
  background-color: #cebfbf;
  border-radius: 8px;
  width: 80%;
  max-width: 1000px;
  margin: 20px auto;
  padding: 20px;
  box-shadow: 0 2px 10px #0000001a;
}

h1, h2 {
  text-align: center;
}

form {
  margin-bottom: 20px;
}

label {
  margin: 8px 0;
  display: block;
}

input, textarea, select {
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 8px 0;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

button:hover {
  background-color: #e1d9de;
}

.delete {
  color: #fff;
  cursor: pointer;
  background-color: red;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
}

.delete:hover {
  background-color: #e1d9de;
}

.board {
  justify-content: space-between;
  gap: 20px;
  margin-top: 20px;
  display: flex;
}

.column {
  background-color: #fff;
  border-radius: 4px;
  flex: 1;
  padding: 10px;
  box-shadow: 0 1px 5px #0000001a;
}

.column h2 {
  text-align: center;
  margin-bottom: 10px;
}

.task {
  background-color: #eef;
  border-radius: 4px;
  margin-bottom: 10px;
  padding: 10px;
  box-shadow: 0 1px 3px #0000001a;
}

#task-list {
  margin-top: 20px;
}

#task-list li {
  border-bottom: 1px solid #ddd;
  padding: 10px;
}
